.user-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;
  padding: 0;
  margin: 0;

  @media (max-width: $vp-1023) {
    column-gap: 23px;
  }
}

.user-list__link {
  font-family: $ff-montserrat-medium;
  font-size: 16px;
  line-height: 19.5px;
  font-weight: 500;
  color: $color-nav;

  @media (max-width: $vp-1023) {
    font-size: 14px;
    line-height: 17px;
  }
}
