.address {
  width: 100%;
  min-height: 546px;
  padding: 120px 20px;
  padding-top: 50px;
}

.address__wrapper {
  margin: 0 auto;
  height: 100%;
  max-width: 1160px;
  display: flex;
  column-gap: 100px;
}

.address__container {
  display: flex;
  flex-direction: column;
  padding-top: 81px;
}

.address__map {
  width: 560px;
}

.address__description {
  @include title-medium;
  margin: 0;
  margin-bottom: 45px;
}

.address__text,
.address__link {
  font-family: $ff-montserrat;
  font-size: 16px;
  line-height: 24px;
}

.address__link {
  margin-bottom: 33px;
}
