.catalog {
  margin: 0 auto;
  height: 100%;
  max-width: 1170px;
  padding: 95px 20px;
}

.catalog__title {
  @include title-semibold;
  margin: 0;
  margin-bottom: 60px;
  text-align: center;
}

.catalog__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.catalog__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  background-color: $text-color;
  padding: 35px 25px;
  width: 260px;
}

.catalog__image {
  object-fit: cover;
  position: relative;
  z-index: -1;
}

.catalog__image-wrapper {
  grid-column: 1/-1;
  grid-row: 1/-1;
}

.catalog__wrapper {
  grid-column: 1/-1;
  grid-row: 1/-1;

  dl {
    @include description-text;
    line-height: 16px;
    color: $color-nav;
    margin: 0;
  }

  dt {
    font-family: $ff-montserrat-medium;
    margin-bottom: 10px;
  }

  dd {
    margin: 0;
    margin-bottom: 20px;
  }
}

.catalog__link {
  @include title-semibold;
  font-size: 14px;
  line-height: 21px;
  padding: 15px 46px;
  background-color: $color-nav;
  text-align: center;
  display: block;
  grid-column: 1/-1;
  grid-row: 2/3;
}

.catalog__wrapper h3 {
  @include title-medium;
  line-height: 22px;
  color: $color-nav;
  margin: 0;
  margin-bottom: 25px;
}
